import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
import { of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';

import { LoggerService } from './logger.service';
import { environment } from '../../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private apiURL: string = `${environment.apiURL}/auth`;
  options: HttpHeaders = new HttpHeaders();
  session: any = {};

  constructor(private http: HttpClient, private loggerService: LoggerService) {
    this.options = new HttpHeaders( {'Content-Type': 'application/json', 'x-access-token': localStorage.getItem('tokenUserLogged')} );
  }

  private logError (error, operation) {
    let message = '';
    let reload = false;
    if (error.status === 404) {
      message = 'Falha ao se comunicar com a API: Rota não encontrada (ERRO 404)';
    } else if (error.status === 401) {
      message = 'Falha ao se comunicar com a API: Token inválido ou expirado';
      reload = this.logoff();
    } else if (error.status === 400) {
      message = `Os dados enviados são inválidos! \nCausa do erro: ${error.error.details[0].message}`;
    } else if (error.status >= 500 && error.status <= 505) {
      message = `Houve algum erro no servidor! Entre em contato com o administrador!`;
    } else {
      message = error.Message;
    }

    this.loggerService.add(`AuthService failed on operation: ${operation}\nMessage: ${message}`);
    if (reload) {
      location.reload();
    }
  }

  private log (mensagem: string) {
    this.loggerService.add('AuthService: ' + mensagem);
  }

  login (user: string, password: string): Observable<any> {
    return this.http.post<any>(`${this.apiURL}/login`, { "user": user, "password": password }, { headers: new HttpHeaders( {'Content-Type': 'application/json'}) }).pipe(
      tap((response: any) => this.log(`login w/ user=${user}`)),
      catchError(this.handleError<any>('login'))
    )
  }

  getSession (): Observable<any> {
    return this.http.get<any>(`${this.apiURL}/session`, { headers: this.options}).pipe(
      tap(_ => this.log(`fetched user logged with token=${localStorage.getItem('tokenUserLogged')}.`)),
      catchError(this.handleError<any>('getSession'))
    )
  }

  logoff () {
    localStorage.removeItem("tokenUserLogged");
    return true;
  }

  userLogged (): boolean {
    let token = localStorage.getItem('tokenUserLogged');
    if (token !== '' && token !== undefined && token !== null) {
      return true;
    } else {
      return false;
    }
  }

  private handleError<T> (operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      this.logError(error, operation);
      return of(result as T);
    };
  }

}
